import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UsersService } from '../users.service';

@Component({
  selector: 'app-create-user',
  templateUrl: './create-user.component.html',
  styleUrls: ['./create-user.component.css'],
})
export class CreateUserComponent implements OnInit {
  @Input('isCreateActive') isCreateActive: boolean;
  @Output() isCreateActiveChanged = new EventEmitter();
  @Output() submitChanged = new EventEmitter();

  constructor(private service: UsersService) {}

  ngOnInit(): void {}

  emitCreateActiveChanged() {
    this.isCreateActive = false;
    this.isCreateActiveChanged.emit(this.isCreateActive);
  }

  createUser(data) {
    let user = data.value;
    this.service.create(user).subscribe((response) => {
      user['id'] = response['id'];
      this.submitChanged.emit(user);
    });
    this.emitCreateActiveChanged();
  }
}
